
import Header from "@/components/common/Header";
import PopupPicker from "@/components/common/PopupPicker";
import UploadImgOne from "@/components/common/UploadImgOne";
import XButton from 'vux/src/components/x-button/index.vue'


import { getUserRule, AuthSubmit } from "@/servers/api";
import { formData } from "@/assets/js/tools";

export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "实名认证",
showLeftBack: true,
showRightMore: false
},
pickValA: [],
pickValB: [],
pickValC: [],
pickValD: [],
pickValE: [],
imgUrl: "",
dataOpA: {
title: "学历",
columns: 2,
data: []
},
dataOpB: {
title: "专业",
columns: 2,
data: []
},
dataOpC: {
title: "爱好",
columns: 2,
data: []
},
dataOpD: {
title: "信仰",
columns: 2,
data: []
},
dataOpE: {
title: "师承",
columns: 4,
data: []
},
dataListArr: []
};
},

components: {
Header,
PopupPicker,
UploadImgOne,
XButton
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {
this.getDataList();
},

methods: {
getImgVal(val) {
console.log(val);
this.imgUrl = val;
},
getPickValA(val) {
console.log(val);
this.pickValA = val;
},
getPickValB(val) {
console.log(val);
this.pickValB = val;
},
getPickValC(val) {
console.log(val);
this.pickValC = val;
},
getPickValD(val) {
console.log(val);
this.pickValD = val;
},
getPickValE(val) {
console.log(val);
this.pickValE = val;
},
getDataList() {
getUserRule({})
.then(res => {
console.log(res);
if (res.result === 1) {

let dataResult = formData(res.data.list);
console.log(dataResult);
dataResult.forEach((item, index) => {
switch (item.name) {
case "学历":
this.dataOpA.data = item.data;
break;
case "专业":
this.dataOpB.data = item.data;
break;
case "爱好":
this.dataOpC.data = item.data;
break;
case "信仰":
this.dataOpD.data = item.data;
break;
case "师承":
this.dataOpE.data = item.data;
break;
default:
return;
}
});
}
})
.catch(err => {
console.log(err);
});
},
showToast(conttentTip) {
this.$vux.toast.text(conttentTip, "middle");
setTimeout(() => {
this.$vux.toast.hide();
}, 1000);
},
veriFormData() {
if (!this.pickValA.length) {
this.showToast("请选择学历");
return;
} else if (!this.pickValB.length) {
this.showToast("请选择专业");
return;
} else if (!this.pickValC.length) {
this.showToast("请选择爱好");
return;
} else if (!this.pickValD.length) {
this.showToast("请选择信仰");
return;
} else if (!this.pickValE.length) {
this.showToast("请选择师承");
return;
} else if (!this.imgUrl) {
this.showToast("请上传身份证照片");
return;
} else {
this.submitVal();
}
},
submitVal() {
this.$vux.loading.show({
text: "正在提交"
});
AuthSubmit({
type: 3,
profession_id: this.pickValA[this.pickValA.length - 1],
education_id: this.pickValB[this.pickValB.length - 1],
hobby_id: this.pickValC[this.pickValC.length - 1],
faith_id: this.pickValD[this.pickValD.length - 1],
teacher_id: this.pickValE[this.pickValE.length - 1],
card_img: this.imgUrl
})
.then(res => {
console.log(res);
if(res){
this.$vux.loading.hide();
if(res.code === 200){

}
}
})
.catch(err => {
console.log(err);
});
}
},

watch: {}
};



